import React from "react"

import Wine from "../components/media/icons/wine.svg"
import Cavatappi from "../components/media/icons/cavatappi.svg"
import Jam from "../components/media/icons/jam.svg"
import Olio from "../components/media/icons/olio.svg"
import Pint from "../components/media/icons/pint.svg"

import style from "./css/prodotti.module.css"
import Layout from "../components/layout/layout"
import Navbar from "../components/navbar/navbar"
import Footer from "../components/footer/footer"

class prodotti extends React.Component {
  render() {
    const products = {
      0: {
        img: Wine,
        name: 'Vino',
        path: 'Vino',
      },
      1: {
        img: Pint,
        name: 'Birra',
        path: 'Birra',
      },
      2: {
        img: Olio,
        name: 'Olio',
        path: 'Olio',
      },
      3: {
        img: Cavatappi,
        name: 'Accessori Vino',
        path: 'accessoriVino',
      },
      4: {
        img: Jam,
        name: 'Bottiglie e Vasetti',
        path: 'bottiglieVasetti',
      },
    }

    return (
      <Layout>
        <Navbar />
        <div className="wrapper">
            <div className={`container ${style.height100}`}>
              <h1><span className={`${style.highlight}`}>Prodotti</span></h1>
              <div className="row">
                {Object.keys(products).map((product, i) => {
                  return (
                    <div className={`${style.product} col`} key={i}>
                      <a href={`/${products[product].path}`}>
                        <div className="container">
                          <div className="row">
                            <div className="col justify-content-center text-center" >
                              <img src={products[product].img} alt="lol" />
                            </div>
                            <h2 className="col-lg-12">{products[product].name}</h2>
                          </div>
                        </div>
                      </a>
                    </div>
                  );
                })}
              </div>
            </div>
        </div>
        <Footer />
      </Layout>
    );
  }
};

export default prodotti;
